import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SharedService } from 'app/service/shared-service/shared.service';
import { Stage } from 'app/entities/report/stage';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Candidateinfo } from 'app/entities/report/candidateinfo';

@Component({
  selector: 'app-add-candidate',
  templateUrl: './add-candidate.component.html',
  styleUrls: ['./add-candidate.component.scss']
})
export class AddCandidateComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<AddCandidateComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private sharedservice:SharedService) { }

stage_id:number;
stage:Stage;
stageList:Array<Stage>=[];

form_addCandidate=new FormGroup({
  candidate_name:new FormControl('',[(Validators.required)])
});

ngOnInit() {
this.stage_id=this.data.stage_id;
this.getStageList();
}

close(){
  this.dialogRef.close();
}

getStageList(){
  this.sharedservice.getStages().subscribe(data =>{
    this.stageList=data;
    this.stage=this.stageList.find(x => x.stage_id==this.stage_id);
  },error=> this.sharedservice.sucessMessage("Something went wrong"))
}

addCandidate(form_addCandidate){
  const candidate=new Candidateinfo(null,this.form_addCandidate.get('candidate_name').value,
                  null,null,this.stage,null);
                  this.dialogRef.close(candidate);
}
}


