import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SharedService } from 'app/service/shared-service/shared.service';
import { Candidateinfo } from 'app/entities/report/candidateinfo';

@Component({
  selector: 'app-candidate-list',
  templateUrl: './candidate-list.component.html',
  styleUrls: ['./candidate-list.component.scss']
})
export class CandidateListComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<CandidateListComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private sharedservice:SharedService) { }

  date=new Date();
  candidateList:Array<Candidateinfo>=[];
  index: number = 1;
  length: number = 0;
  pagesize: number = 5;
  pageSizeOptions = [5, 10];

ngOnInit() {
  this.length=this.data.candidateList.length;
    this.candidateList=this.data.candidateList.slice((this.index-1)*this.pagesize,
    ((this.index-1)*this.pagesize+this.pagesize));
}

close(){
  this.dialogRef.close();
}

onPaginateChange(event) {
  this.index = event.pageIndex + 1;
  this.pagesize = event.pageSize;
  this.ngOnInit();
}
}
