import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PartnerProfileComponent } from './partner-profile/partner-profile.component';
import { MatButtonModule } from '@angular/material/button';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatIconModule } from '@angular/material/icon';
import { MatSelectModule } from '@angular/material/select';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';
import { MatTabsModule } from '@angular/material/tabs';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseDemoModule } from '@fuse/components/demo/demo.module';
import { FuseHighlightModule, FuseWidgetModule } from '@fuse/components';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { MatInputModule } from '@angular/material/input';
import { DataTablesModule } from 'angular-datatables';
import { RouterModule } from '@angular/router';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatDialogModule } from '@angular/material/dialog';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatPaginatorModule } from '@angular/material/paginator';
import { PartnerDashboardComponent } from './partner-dashboard/partner-dashboard.component';
import { QuantitativeComponent } from './quantitative/quantitative.component';
import { QualtitativeComponent } from './qualtitative/qualtitative.component';
import { AddQualitativeComponent } from './add-qualitative/add-qualitative.component';
import { UpdateQualitativeComponent } from './update-qualitative/update-qualitative.component';
import { CKEditorModule } from 'ng2-ckeditor';
import { AddQuantitativeComponent } from './add-quantitative/add-quantitative.component';
import { UpdateQuantitativeComponent } from './update-quantitative/update-quantitative.component';
import { CandidateListComponent } from './candidate-list/candidate-list.component';
import { AddCandidateComponent } from './add-candidate/add-candidate.component';
import { DbCandidateListComponent } from './db-candidate-list/db-candidate-list.component';
import { DbAddCandidateComponent } from './db-add-candidate/db-add-candidate.component';
import { PartnerReportComponent } from './partner-report/partner-report.component';
import { ViewReportComponent } from './view-report/view-report.component';
import { PartnerGuardService } from 'app/service/guard/partner-guard.service';

const routes = [

  {
    path: 'dashboard',
    component: PartnerDashboardComponent,canActivate:[PartnerGuardService]
  },
  {
    path: 'profile',
    component: PartnerProfileComponent,canActivate:[PartnerGuardService]
  },
  {
    path: 'others',
    component: PartnerReportComponent,canActivate:[PartnerGuardService]
  },
  {
    path: 'report',
    children: [
      { path: '', redirectTo: 'quantitative', pathMatch: 'full' },
      { path: 'add-quantitative', component: AddQuantitativeComponent,canActivate:[PartnerGuardService] },
      { path: 'update-quantitative', component: UpdateQuantitativeComponent,canActivate:[PartnerGuardService] },
      { path: 'quantitative', component: QuantitativeComponent,canActivate:[PartnerGuardService] },
      { path: 'qualitative', component: QualtitativeComponent,canActivate:[PartnerGuardService] },
      { path: 'add-qualitative', component: AddQualitativeComponent,canActivate:[PartnerGuardService] },
      { path: 'update-qualitative', component: UpdateQualitativeComponent,canActivate:[PartnerGuardService] }]
  },


];


@NgModule({
  declarations: [PartnerProfileComponent, PartnerDashboardComponent,
    QualtitativeComponent, QuantitativeComponent, PartnerProfileComponent, 
    AddQualitativeComponent, UpdateQualitativeComponent, AddQuantitativeComponent, UpdateQuantitativeComponent, CandidateListComponent, AddCandidateComponent, DbCandidateListComponent, DbAddCandidateComponent, PartnerReportComponent, ViewReportComponent
  ],
  imports: [
    CommonModule,
    NgbModule,
    MatDialogModule,
    RouterModule.forChild(routes),
    MatButtonModule,
    MatButtonToggleModule,
    MatIconModule,
    MatSelectModule,
    MatSlideToggleModule,
    MatTabsModule,
    MatInputModule,
    NgxChartsModule,
    FuseSharedModule,
    FuseDemoModule,
    FuseWidgetModule,
    FuseHighlightModule,
    DataTablesModule,
    MatTooltipModule,
    MatDatepickerModule,
    MatPaginatorModule,
    CKEditorModule
  ],
  entryComponents: [
    AddCandidateComponent,
    CandidateListComponent,
    DbCandidateListComponent,
    DbAddCandidateComponent,
    ViewReportComponent
  ],
})
export class PartnerModule { }
