import { Component, OnInit } from '@angular/core';
import { SharedService } from 'app/service/shared-service/shared.service';
import { FormControl } from '@angular/forms';
import { QuantitaiveInfo } from 'app/entities/report/quantitaive-info';
import * as CanvasJS from '../../../../assets/canvasjs.min';

@Component({
  selector: 'app-partner-dashboard',
  templateUrl: './partner-dashboard.component.html',
  styleUrls: ['./partner-dashboard.component.scss']
})
export class PartnerDashboardComponent implements OnInit {

  constructor(private sharedservice:SharedService) { }

  partner_id:number;
	team_status:boolean=true;
	admin_status:boolean=false;
	from_date:Date;
  to_date:Date;
  chart:any;

  info1:any;
  info2:any;
  partner:any;
  f_date: FormControl = new FormControl();
  t_date: FormControl = new FormControl();

  ngOnInit() {
    this.partner_id=this.sharedservice.getPartnerID();
    this.getPartnerList();
    this.getChartInfo();
  }

  getChartInfo(){
    const condition=new QuantitaiveInfo(this.partner_id,this.team_status,this.admin_status,
            this.from_date,this.to_date,null);
    this.sharedservice.getGraphInfo(condition).subscribe(data => {
      this.chart=data;
      this.drawChart(this.chart);
     },error => this.sharedservice.sucessMessage("Something Went Wrong"))
  }

  getPartnerList(){
    this.sharedservice.getRelatedPartnerForDashboard(this.sharedservice.getPartnerID()).subscribe(data => {
      this.partner=data;
    },error => this.sharedservice.sucessMessage("Something Went Wrong"))
  }

  setPartnerID(partner_id:number){
    this.partner_id=partner_id;
    this.getChartInfo();
  }

  drawChart(chart){
    let dchart = new CanvasJS.Chart("chartContainer", {
      animationEnabled: true,
      title: {
        text: 'Report Distribution',
        fontFamily: "tahoma",
        fontSize:25
      },
      axisY:{ 
        gridThickness: 0 
     },
      data: [{
        type: "column",
        dataPoints: [
          { y: chart.cv_sent, label: "CV Sent" },
          { y: chart.interview, label: "Interview" },
          { y: chart.offer, label: "Offer" },
          { y: chart.joining, label: "Joining" }
        ]
      }]
    });
      
    dchart.render();
  }

  toggle(){
    this.team_status=!this.team_status;
    this.getChartInfo();
  }

  setDate(){
    if(this.f_date.value!=null && this.t_date.value!=null){
      this.from_date=this.f_date.value;
      this.to_date=this.t_date.value;
      this.getChartInfo();
    }
  }

}
