import { Component, OnInit, Inject } from '@angular/core';
import { Qualitative } from 'app/entities/report/qualitative';
import { Quantitative } from 'app/entities/report/quantitative';
import { MatDialogRef, MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SharedService } from 'app/service/shared-service/shared.service';
import { DbCandidateListComponent } from '../db-candidate-list/db-candidate-list.component';

@Component({
  selector: 'app-view-report',
  templateUrl: './view-report.component.html',
  styleUrls: ['./view-report.component.scss']
})
export class ViewReportComponent implements OnInit {

  report_status:boolean;
  id:number;
  index: number = 1;
  length: number = 0;
  pagesize: number = 5;
  pageSizeOptions = [5, 10];
  qualitative: Qualitative;
  quantitative: Quantitative;
  distribution: any;

  constructor(public dialogRef: MatDialogRef<ViewReportComponent>,private dialog: MatDialog,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private sharedservice:SharedService) { }

  ngOnInit() {
    this.id=this.data.id;
    this.report_status=this.data.status;
    if(this.report_status){
      this.getQualitatieData(this.id);
    }
    else{
      this.getQuantitativeData(this.id);
    }
  }

  getQualitatieData(id:number){
    this.sharedservice.getQualitativeByID(id).subscribe(data => {
      this.qualitative = data;
    },error => this.sharedservice.sucessMessage("Something went wrong"))
  }

  getQuantitativeData(id:number){
    this.sharedservice.getQuantitativeByID(id).subscribe(data => {
      this.quantitative = data;
      this.getStageDistribution(id);
    }, error => this.sharedservice.sucessMessage(error.error.message))
  }

  getStageDistribution(id: number) {
    this.sharedservice.getStageDistribution(this.id).subscribe(data => {
      this.distribution = data;
    }, error => this.sharedservice.sucessMessage("Something went wrong"))
  }

  getCandidates(){
    const dialogRef = this.dialog.open(DbCandidateListComponent,{
      width: '550px',
      height: '420px',
      data: { id: this.id }
    })
  }


  close(){
    this.dialogRef.close();
  }

}

