import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SharedService } from 'app/service/shared-service/shared.service';
import { Candidateinfo } from 'app/entities/report/candidateinfo';
import { Pagination } from 'app/entities/condition/pagination';

@Component({
  selector: 'app-db-candidate-list',
  templateUrl: './db-candidate-list.component.html',
  styleUrls: ['./db-candidate-list.component.scss']
})
export class DbCandidateListComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<DbCandidateListComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private sharedservice:SharedService) { }
    
  candidateList:any[];
  id:number;
  index: number = 1;
  length: number = 0;
  pagesize: number = 5;
  pageSizeOptions = [5, 10];

ngOnInit() {
  this.id=this.data.id;
  this.getCandidateList();
}

getCandidateList(){
  const condition=new Pagination(this.index,this.pagesize,null);
  this.sharedservice.getCandidateList(condition,this.id).subscribe(data => {
    this.candidateList=data.data;
    this.length=data.length;
  },error=>this.sharedservice.sucessMessage(error.error.message))
}

close(){
  this.dialogRef.close();
}

onPaginateChange(event) {
  this.index = event.pageIndex + 1;
  this.pagesize = event.pageSize;
  this.getCandidateList();
}
}
