import { Component, OnInit, Inject } from '@angular/core';
import { Stage } from 'app/entities/report/stage';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SharedService } from 'app/service/shared-service/shared.service';
import { Candidateinfo } from 'app/entities/report/candidateinfo';
import { Quantitative } from 'app/entities/report/quantitative';

@Component({
  selector: 'app-db-add-candidate',
  templateUrl: './db-add-candidate.component.html',
  styleUrls: ['./db-add-candidate.component.scss']
})
export class DbAddCandidateComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<DbAddCandidateComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private sharedservice:SharedService) { }

stage_id:number;
stage:Stage;
id:number;
stageList:Array<Stage>=[];

form_addCandidate=new FormGroup({
  candidate_name:new FormControl('',[(Validators.required)])
});

ngOnInit() {
this.stage_id=this.data.stage_id;
this.getStageList();
}

close(){
  this.dialogRef.close();
}

getStageList(){
  this.sharedservice.getStages().subscribe(data =>{
    this.stageList=data;
    this.stage=this.stageList.find(x => x.stage_id==this.stage_id);
  },error=> this.sharedservice.sucessMessage("Something went wrong"))
}

addCandidate(form_addCandidate){
  const candidate=new Candidateinfo(null,this.form_addCandidate.get('candidate_name').value,null,null,
  this.stage,new Quantitative(this.id,null,null,null,null,null,null,null,null));
  this.dialogRef.close(candidate);
}
}

