import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Qualitative } from 'app/entities/report/qualitative';
import { Partner } from 'app/entities/partner/partner';
import { Router } from '@angular/router';
import { SharedService } from 'app/service/shared-service/shared.service';

@Component({
  selector: 'app-add-qualitative',
  templateUrl: './add-qualitative.component.html',
  styleUrls: ['./add-qualitative.component.scss']
})
export class AddQualitativeComponent implements OnInit {

  
  constructor(private sharedservice:SharedService,
              private router:Router) { }


  ckeConfig: any;
  mycontent: string;

  ngOnInit() {
    this.ckeConfig = {
      allowedContent: false,
      forcePasteAsPlainText: true,
      toolbar: 'Basic',
      keystrokes: [],
    //   removeButtons: 'Cut,Copy,Undo,Redo,Paste,PasteText,PasteFromWord,Save,Source,NewPage,Preview,Print,Templates,Find,Replace,SelectAll,Scayt,Form,Checkbox,Radio,TextField,Textarea,Select,Button,ImageButton,HiddenField,CopyFormatting,RemoveFormat,CreateDiv,BidiLtr,BidiRtl,Language,Link,Unlink,Anchor,Image,Flash,Table,HorizontalRule,Smiley,SpecialChar,PageBreak,Iframe,Styles,Format,TextColor,Maximize,ShowBlocks,About,BGColor'
     };
  }

  form_addreport=new FormGroup({
    title:new FormControl('',[(Validators.required)]),
    editor:new FormControl('',[(Validators.required)])
  });

  addQualitative(form_addpartner){
    const qualitative=new Qualitative(null,
      new Partner(this.sharedservice.getPartnerID(),null,null,null,null,null,null,null,null,null,null,null),
      this.form_addreport.get('title').value,this.form_addreport.get('editor').value,null,null);
      this.sharedservice.saveQualitative(qualitative).subscribe(data => {
        this.sharedservice.sucessMessage("Report Added")
        this.router.navigate(["/partner/report/qualitative"]);
      },error=> this.sharedservice.sucessMessage(error.error.message))
      
  }



  

}
