import { Component, OnInit } from '@angular/core';
import { SharedService } from 'app/service/shared-service/shared.service';
import { SupportService } from 'app/service/shared-service/support.service';
import { Router } from '@angular/router';
import { Pagination } from 'app/entities/condition/pagination';

@Component({
  selector: 'app-qualtitative',
  templateUrl: './qualtitative.component.html',
  styleUrls: ['./qualtitative.component.scss']
})
export class QualtitativeComponent implements OnInit {

  constructor(private sharedservice: SharedService,
    private supportservice: SupportService,
    private router: Router) { }


  qualitative: any;
  index: number = 1;
  length: number = 0;
  pagesize: number = 5;
  pageSizeOptions = [5, 10, 15, 20];

  ngOnInit() {
    this.getQualitativeList();  
  }

  getQualitativeList() {
    const condition = new Pagination(this.index, this.pagesize, null);
    this.sharedservice.getQualitativeList(condition,this.sharedservice.getPartnerID()).subscribe(data => {
      this.qualitative = data.data;
      this.length=data.length;
    }, error => this.sharedservice.sucessMessage(error.error.message))
  }

  onPaginateChange(event) {
    this.index = event.pageIndex + 1;
    this.pagesize = event.pageSize;
    this.getQualitativeList();
  }


  edit(id: number) {
    this.supportservice.changeData(id);
    this.router.navigate(["/partner/report/update-qualitative"]);
  }

}
