import { Component, OnInit } from '@angular/core';
import { SharedService } from 'app/service/shared-service/shared.service';
import { MatDialog } from '@angular/material/dialog';
import { FormControl } from '@angular/forms';
import { QuantitaiveInfo } from 'app/entities/report/quantitaive-info';
import { Pagination } from 'app/entities/condition/pagination';
import { ViewReportComponent } from '../view-report/view-report.component';

@Component({
  selector: 'app-partner-report',
  templateUrl: './partner-report.component.html',
  styleUrls: ['./partner-report.component.scss']
})
export class PartnerReportComponent implements OnInit {

  constructor(private sharedservice: SharedService,
    private dialog: MatDialog) { }

  f_date: FormControl = new FormControl();
  t_date: FormControl = new FormControl();
  partner: any;
  from_date: Date;
  to_date: Date;
  report_type: number;
  partner_id: number;
  qualitative: Array<any> = [];
  quantitative: Array<any> = [];
  index: number = 1;
  text = "";
  length: number = 0;
  pagesize: number = 5;
  pageSizeOptions = [5, 10, 15, 20];

  ngOnInit() {
    this.getPartnerList();
  }

  getPartnerList() {
    this.sharedservice.getRelatedPartnerForDashboard(this.sharedservice.getPartnerID()).subscribe(data => {
      this.partner = data;
    }, error => this.sharedservice.sucessMessage("Something Went Wrong"))
  }

  setPartnerID(partner_id: number) {
    this.partner_id = partner_id;
    this.getTableData();
  }

  getTableData() {
    this.qualitative.length = 0;
    this.quantitative.length = 0;
    if (this.report_type == 0) {
      this.getQualitativeData()
    }
    else {
      this.getQuantitativeData()
    }
  }

  setReportType(report_type: number) {
    this.report_type = report_type;
  }

  onPaginateChange(event) {
    this.index = event.pageIndex + 1;
    this.pagesize = event.pageSize;
    this.getTableData();
  }

  getQualitativeData() {
    const qunatitativeInfo = new QuantitaiveInfo(this.partner_id, false, false,
      this.from_date, this.to_date, new Pagination(this.index, this.pagesize, null));
    this.sharedservice.getQualitativeReport(qunatitativeInfo).subscribe(data => {
      this.qualitative = data.data;
      this.length = data.length;
    }, error => this.sharedservice.sucessMessage("Something Went Wrong"))
  }

  getQuantitativeData() {
    const qunatitativeInfo = new QuantitaiveInfo(this.partner_id, false, false,
      this.from_date, this.to_date, new Pagination(this.index, this.pagesize, null));
    this.sharedservice.getQuantitativeReport(qunatitativeInfo).subscribe(data => {
      this.quantitative = data.data;
      this.length = data.length;
    }, error => this.sharedservice.sucessMessage("Something Went Wrong"))
  }

  setDate() {
    if (this.f_date.value != null && this.t_date.value != null) {
      this.from_date = this.f_date.value;
      this.to_date = this.t_date.value;
      this.getTableData();
    }
  }

  view(id: number) {
    var report_status=false;
    report_status=this.report_type==0?true:false;
    const dialogRef = this.dialog.open(ViewReportComponent, {
      width: '700px',
      height: '220px',
      data: { id: id ,status:report_status}
    })
  }
}
