import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AddCandidateComponent } from '../add-candidate/add-candidate.component';
import { MatDialog } from '@angular/material/dialog';
import { Candidateinfo } from 'app/entities/report/candidateinfo';
import { CandidateListComponent } from '../candidate-list/candidate-list.component';
import { Quantitative } from 'app/entities/report/quantitative';
import { Partner } from 'app/entities/partner/partner';
import { SharedService } from 'app/service/shared-service/shared.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-quantitative',
  templateUrl: './add-quantitative.component.html',
  styleUrls: ['./add-quantitative.component.scss']
})
export class AddQuantitativeComponent implements OnInit {

  constructor(private dialog: MatDialog,private sharedservice:SharedService,
    private router:Router) { }

  candidateList:Array<Candidateinfo>=[];

  form_addreport=new FormGroup({
    client_name:new FormControl('',[(Validators.required)]),
    position_name:new FormControl('',[]),
    mapping:new FormControl('',[]),
    comment:new FormControl('',[])
  });

  ngOnInit() {
  }

 
  addCandidate(stage_id:number){
    const dialogRef = this.dialog.open(AddCandidateComponent, {
      width: '600px',
      data: { stage_id: stage_id}
    }).afterClosed().subscribe(response => {
      if(response!=null){
        this.candidateList.push(response);
      }
    });
  }

  getLength(stage_id:number){
    if(this.candidateList.length>0)
    return this.candidateList.filter( x=> x.stage.stage_id==stage_id).length;
    else
    return 0;
  }

  getCandidateList(){
    const dialogRef = this.dialog.open(CandidateListComponent, {
      width: '550px',
      height:'420px',
      data: { candidateList: this.candidateList}
    })
  }

  addQualitative(form_addpartner){
    const quantitative=new Quantitative(null,this.form_addreport.get('client_name').value,
    this.form_addreport.get('position_name').value,this.candidateList,
    this.form_addreport.get('mapping').value,this.form_addreport.get('comment').value,
    null,null,new Partner(this.sharedservice.getPartnerID(),null,null,null,
    null,null,null,null,null,null,null,null));
    this.sharedservice.saveQuantitative(quantitative).subscribe(data => {
      this.sharedservice.sucessMessage("Report Added");
      this.router.navigate(["/partner/report/quantitative"]);
    },error => this.sharedservice.sucessMessage(error.error.message))
  }

}
